var listDate = [{ index: 41, publisher: '张婷文', group: '麦田', content: '为张文珊（张婷文姐妹的妹妹）祷告，求神让她能够明白福音，通过这次参加婷文的婚礼能够思考这份信仰并且心里接受。也求神医治她的身体，她前段时间做了假牙现在整个脸过敏，因为她对金属过敏，假牙是金属质，反复过敏两次，而且她因为身体关系，止血比较慢，拔牙做牙齿的时候都血流不止，就算已经拔牙几周了，现在还在断断续续流血。婷文想到她如果能够信主，不一定要被医治但有信靠的神也不至于如此惶恐不安。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '张文珊在我结婚的当天，脸上就好了很多，当天涂了药，后来就不吃药也不涂，过敏的脸差不多都好了。牙龈也不出血，只偶尔会疼之前是一整天都在疼。荣耀归给 神！', updateDate: '2017-03-03', createDate: '2017-03-03', endDate: '2017-03-17' }, { index: 51, publisher: '杰军', group: '活石', content: '为黎大毕业生唯唯姐妹祷告，因为方便服侍选择东海一家室内设计公司，但是老板非常迷信，拜偶像严重，本来想离东海近方便，没想到经常加班，没时间聚会，现已离职。为下一份工作祷告，希望能方便服侍。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经找到工作', updateDate: '2017-03-08', createDate: '2017-03-08', endDate: '2017-03-17' }, { index: 5, publisher: '董中民', group: '教会', content: '为代祷网站招募同工代祷，需要有信息收集和整理、更新的同工，还需要一名代祷信息跟进反馈的结果的同工，要求：细心、了解excel表格的简单使用，如果你有这个感动请联系中民弟兄', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经有人回应了', updateDate: '2017-02-08', createDate: '2017-02-08', endDate: '2017-03-17' }, { index: 12, publisher: '祖华', group: '但以理', content: '更新1：天天后来没有去医院，第二天有些好转！感冒还没完全好，继续代祷！谢谢；祖华代祷信息：为我女儿天天代祷，两周八，感冒两周，咳嗽有痰，嗜睡烦躁，食欲差，口臭，其中有发烧39℃，已退。医生建议去医院检查拍片抽血，看看是不是肺炎！我现在在福清，明天上午带她去医院看看！', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经康复了', updateDate: '2017-02-09', createDate: '2017-02-09', endDate: '2017-03-17' }, { index: 7, publisher: '杨艳', group: '教会', content: '为燕霞的女儿祷告。燕霞的信息：幼儿急诊没逃过，现在又是轮状肠胃炎看着她真让人煎熬…地上的苦难靠着主都会过去，更令人安慰的是，我们的盼望在天上，那是永恒的喜乐。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经康复了', updateDate: '2017-02-08', createDate: '2017-02-08', endDate: '2017-03-17' }, { index: 35, publisher: '董中民', group: '麦田', content: '更新：现在去宁德医院了。为玉兰的嫂子（卢冰冰）代祷，生产后十多天了，还没有办法正常排小便，为她产后的情绪祷告，也为玉兰的哥哥代祷，可以更好的体贴妻子', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经康复，明天（2017-03-07）可以出院了', updateDate: '2017-03-01', createDate: '2017-02-27', endDate: '2017-03-17' }, { index: 8, publisher: '杨艳', group: '教会', content: '请弟兄姐妹为燕霞姐妹的舅舅陈天波祷告，现在癌症晚期，非常痛苦。燕霞想在3月底回福建给他传福音，愿主预备他的心，存留他的生命，带领燕霞他们平安回来，有机会给舅舅传福音。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经过世了', updateDate: '2017-02-08', createDate: '2017-02-08', endDate: '2017-03-17' }, { index: 45, publisher: '韩弟兄', group: '教会', content: '更新：确诊地中海贫血，目前还在住院，感染，另外肝肿大，9号还要做头部核磁共振，医生说因为缺氧太久，需要看下是否颅内出血之类的，为恩奇父母祷告。为小“恩奇”的生命祷告，刚刚出生的他，这周还要一系列婴儿体侧，求主保守', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '已经出院在家，虽然医生说有点点小问题，但感恩，吃好喝好睡好，很可爱……继续求主保守！ ', updateDate: '2017-03-09', createDate: '2017-03-04', endDate: '2017-03-17' }, { index: 42, publisher: '谢林莉', group: '但以理', content: '谢林莉：最近弟媳（曹礼燕）爸爸因心脏搭桥出问题，出现胸腔大量积水，现在住院治疗。前些天一天抽了2000毫升的积水，求主医治并保守胸腔积液引流后不会出现再积水。', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '已经出院了', updateDate: '2017-03-03', createDate: '2017-03-03', endDate: '2017-03-17' }, { index: 32, publisher: '祖华', group: '其它', content: '更新：哥哥已经回来了；晓蕾姐妹：亲爱的弟兄姐妹们，感谢神赐我们双胞胎，相信神所赐的神亲自保守，请切切为我们的孩子祷告，原本在身边两天的弟弟结果是情况比在温箱里的哥哥严重，今天两个都开始照蓝光，孩子会很难受，求神医治保守他们。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '兄弟俩都回到家中了，情况稳定', updateDate: '2017-02-27', createDate: '2017-02-20', endDate: '2017-03-17' }, { index: 36, publisher: '董中民', group: '毕业生', content: '为雨佳姐妹的生命成长稳定，教会的诗班及司琴服侍委身，婚姻的预备等代祷。', label: '婚姻', isUrgent: false, isLong: true, isEnd: true, archiveContent: '谢谢大家代祷，昨天我顺利成为瑞景教会正式诗班员了', updateDate: '2017-03-01', createDate: '2017-03-01', endDate: '2017-03-21' }, { index: 31, publisher: '守玉', group: '多加组', content: '为着多加组的秀丽代祷，寒假回家之后好像发生了什么事情，现在不愿意回小组，她家里好像亲戚是真耶稣教的', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '现在有正常聚会了', updateDate: '2017-02-20', createDate: '2017-02-20', endDate: '2017-03-17' }, { index: 26, publisher: '鸿赐', group: '麦田', content: '鸿赐二姐婆婆(培英姐妹)今天去医院检查，发现心脏有问题需要手术，因为长辈本生有糖尿病开刀的话会血流不止，医生有提出新的治疗方案，但不知道适合与否，求神赐信心给他们，引导她的路，使他们一家经历神！', label: '疾病', isUrgent: false, isLong: true, isEnd: true, archiveContent: '现在可能要拖，如果没有什么大碍可能就不做，因为怕到时起不来，因为很伤身', updateDate: '2017-02-16', createDate: '2017-02-16', endDate: '2017-03-24' }, { index: 30, publisher: '守玉', group: '多加组', content: '雯雯的妈妈不同意雯雯小姑过来参加我和雯雯的婚礼，因为雯雯小姑在年轻不信主的时候离过婚，雯雯妈妈觉得雯雯小姑过来不吉利，但雯雯小姑很想过来参加我和雯雯的婚礼。求神开路，怜悯我和雯雯，让我和雯雯说合宜的话，能够打开雯雯妈妈的心结，也求神柔软雯雯妈妈的心。', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '我妈妈还是不愿意小姑过来，不过我小姑理解妈妈，还安慰我，神也调整我小姑很多，她一直为我们祷告。我表妹莉莉（小姑女儿）会过来泉州', updateDate: '2017-02-20', createDate: '2017-02-20', endDate: '2017-03-17' }, { index: 28, publisher: '谢林莉', group: '但以理', content: '谢林莉：最近工作量很大，工作压力也大，之前的部门解散了，数千份文档、图纸要重新做好归到档案室。求主帮助！', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '我估计要忙到5月份，感谢神！公司分配了两位同事在帮我。', updateDate: '2017-03-01', createDate: '2017-02-20', endDate: '2017-03-17' }, { index: 14, publisher: '婷文', group: '麦田', content: '请弟兄姐妹为张婷文的外甥女施韵昕（一岁）代祷。她会流鼻涕，早上起床期间都会咳嗽，白天偶尔咳嗽，且有痰。已经持续了三四个月，医生已经确证是肺炎。已经吃药一个月了，现在停药，还没有好。若不是神医治，连医生也没有权柄可以医好施韵昕，求神动工医治这个孩子，也感动姐姐姐夫迫切祷告（基督徒）。', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '我的外甥女施韵昕，喉咙里的痰都消失了，还会流鼻水，很稀很稀的那种，需要继续为她的身体祷告。感谢主，本来我一点信心都没有，但是藉着这两个亲人的医治，我的信心被重新建立，祷告更加有力量，并且更加认识到神真是满有怜悯', updateDate: '2017-02-09', createDate: '2017-02-09', endDate: '2017-03-17' }, { index: 4, publisher: '董中民', group: '教会', content: '更新：1、为已经报名的人员代祷，使他们可以安排好时间参加，为老师代祷，路上的安全，主题信息，求主预备。为奔跑人生营会祷告，为人员报名，后勤安排，来回安全，为已经报名接力的弟兄姐妹的时间祷告，很多人答应了，又觉得到时可能有事，求预备我们的心和时间', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '顺利结束', updateDate: '2017-02-20', createDate: '2017-02-08', endDate: '2017-03-17' }, { index: 43, publisher: '董中民', group: '麦田', content: '更新：需要代祷，各方人员来回路上的平安，一切事项的安排，尊主为大，希望能荣耀神，也看到主里婚姻的不一样。志超婷文（3.11周六上午，地点新殿），为天气、后勤人员、新人父母（婷文父母还没有信主）、车辆安全', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '顺利结束', updateDate: '2017-03-09', createDate: '2017-03-03', endDate: '2017-03-17' }, { index: 15, publisher: '祖华', group: '其它', content: '更新：水水好多了，没有自杀，但这种抑郁辅导个案很难一下就看到果效，何况只是远距离透过微信，谢谢大家的代祷和关心！听见机构 钟姐妹:日前在微信后台留言的某位粉絲「水水」确认为中度抑郁症，自杀过一次，仍有强烈自杀念头。昨天终于听劝去看了医生，目前服药中。我会持续跟进辅导，虽然透过微信与时差难度加增，但她似乎挺接纳我的引导。恳请大伙儿代祷，求主赐我智慧，更求主完全医治她，里外更新。谢谢！', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '如果你对他/她灵活有负担请继续祷告', updateDate: '2017-02-20', createDate: '2017-02-09', endDate: '2017-03-17' }, { index: 84, publisher: '福东', group: '其它', content: '更新：奇鹏回复：今天一直没有机会单独，我回去姑姑一直守着，没有机会，下午我再过去的时候，只剩下我爸爸在！我之前其实就有跟奶奶传，但是没有带她祷告！这次先确认奶奶还是比较愿意，我心里有一个感动，就是先跟我爸说我要做什么，然后我说我要用基督教的方式来带奶奶祷告！所以我就可以放声的跟奶奶说，并带领祷告===>为奇鹏弟兄奶奶代祷，刚接到电话说我奶奶快不行了，所以我要今天回家再跟她传福音，也为我祷告。。谢谢', label: '福音', isUrgent: true, isLong: false, isEnd: true, archiveContent: '奇鹏：我奶奶已经可以稍微进食，我已经回晋江。奇鹏反馈 ：感谢主，奶奶安息主怀，她的笑容成为我的安慰', updateDate: '2017-03-17', createDate: '2017-03-16', endDate: '2017-03-20' }, { index: 50, publisher: '胡映苇', group: '青草地', content: '我（映苇）这学期是要转专业的，然后我有交申请上去，他们规定就是3.7号前如果审核过的话就会把申请表给拟转学院，但是到现在我也没有收到消息，我不知道是什么情况，希望代祷，不论审核是否成功，也能给我一些消息，不然不知道什么结果总是担忧', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '没有转成功', updateDate: '2017-03-08', createDate: '2017-03-08', endDate: '2017-03-17' }, { index: 6, publisher: '董中民', group: '教会', content: '为守玉雯雯（3月5号下午，地点新殿），为天气、后勤人员、新人父母（雯雯父母还没有信主）、车辆安全', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '婚礼完成，感谢主，双方父母都很满意', updateDate: '2017-03-03', createDate: '2017-02-08', endDate: '2017-03-17' }, { index: 38, publisher: '刘丽霞', group: '橄榄树', content: '丽霞代祷事项：为来参加婚礼的没有信主人群祷告。感觉有争战。。我大表姐二表姐本来答应要参加婚礼的，但估计是我外婆又说什么导致她们决定只参加婚宴，我现在是感冒没声音还有脸上过敏，丽霞思强（3月4号，地点惠安）婚礼，为天气、前期安排、车辆安全', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '婚礼顺利完成', updateDate: '2017-03-01', createDate: '2017-03-01', endDate: '2017-03-17' }, { index: 73, publisher: '梁茜', group: '麦田', content: '梁茜姐妹代祷事项：明天志超婷文婚礼，我会带四个朋友来参加，这可能是大学最后的机会带他们来教会听福音，求神柔软他们的心，求神让真理能进到他们心里面，也让他们能对婚姻有盼望。四个人中有一个是同性恋，从小在家里受过很多伤害，求神能感动她，转回她的心', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '婚礼结束，如果你对她们的灵魂还有负担，请继续祷告', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '2017-03-17' }, { index: 10, publisher: '振营', group: '其它', content: '请弟兄姊妹为单身弟兄姐妹的择偶恋爱的事情祷告。邹老师请我们特别为厦大毕业的程媛还有雯雯丹萍蔚蓝的婚姻切切代祷', label: '婚姻', isUrgent: false, isLong: true, isEnd: true, archiveContent: '很久没有更新了，有感动继续祷告', updateDate: '2017-02-08', createDate: '2017-02-08', endDate: '2017-03-21' }, { index: 58, publisher: '波文', group: '闽科', content: '更新:波文弟兄工作的事情，刚刚波文有问了导师，他说不出意外明天(3.22)会通知去面试，可能面试完，若是通过了，下周就开始工作了，也为波文面试的时候有个好心态，有智慧和应聘的人交流===>更新（还有一周出结果）为苑波文弟兄代祷，目前面临找工作的问题，本来在学校有一份工作，之前岗位不对外招聘，经过老师要求，又重新向社会招聘，已经投了简历，等待面试，请大家代祷，目前闽科很需要有人配搭步建弟兄服侍，平常就步建弟兄一人，比较孤单，没力量，所以我也凭着感动，也就回来应聘，请主代领。', label: '个人', isUrgent: true, isLong: false, isEnd: true, archiveContent: '工作的话，应该是我要重新找了，因为人事那边是说招个有经验的，所以目前不会考虑应届的', updateDate: '2017-03-22', createDate: '2017-03-09', endDate: '2017-03-23' }, { index: 80, publisher: '祖华', group: '但以理', content: '为着上周六（3.11）刚来的几个慕道友代祷，慕道友的跟进及探访要怎么样，求主赐下有负担的弟兄姐妹！', label: '事工', isUrgent: false, isLong: true, isEnd: true, archiveContent: '祷告信息不够具体，先关闭，有感动继续祷告', updateDate: '2017-03-14', createDate: '2017-03-14', endDate: '2017-03-21' }, { index: 13, publisher: '祖华', group: '其它', content: '陈老师:沒想到主診医生今天說我父親年老患病已到了永久性高額傷殘地步 心里為父親的身體狀況感到難過 求主怜憫他並減少他身上的痛楚', label: '', isUrgent: false, isLong: true, isEnd: true, archiveContent: '陈老师：父親从一月底就在医院渡過， 剛剛精神飽滿地從医院回到家中了 感謝大家的代禱', updateDate: '2017-02-09', createDate: '2017-02-09', endDate: '2017-03-17' }, { index: 52, publisher: '杰军', group: '活石', content: '为黎大毕业生唯唯姐妹的爸爸祷告，在家装修时不小心摔了，肋骨裂开了还是怎么的，很疼，要在家修养一个月才能好。', label: '疾病', isUrgent: true, isLong: false, isEnd: true, archiveContent: '差不多恢复了', updateDate: '2017-03-08', createDate: '2017-03-08', endDate: '2017-03-20' }, { index: 111, publisher: '向锋', group: '其它', content: '张向锋弟兄：一直有想回泉州的心，若是你有感动，可以为我祷告，求神帮助道路，预备在泉州的工作，虽然在苏州有稳定的聚会，查经，祷告，但是心里还是切切感觉泉州团契好！家人对回去也有点小意见，求神预备合适机会，合适时间，也帮助我自己能够去经历祂！', label: '个人', isUrgent: false, isLong: false, isEnd: false, archiveContent: ' ', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 94, publisher: '祖华', group: '但以理', content: '1、为道辉及王欣的婚后生活及前面服侍道路代祷2、为思强丽霞结婚后的信仰生活；为思强的爸爸的信主来祷告', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 60, publisher: '祖华', group: '教会', content: '1.求主保守每一位在外服事的同工身心健壮、出入平安。2.求圣灵同在、恩膏为期两个月的丧亲事工，亲自对每位痛失家人的心说话，也赐下恩言柔软当地教会弟兄姐妹们的心肠，明白如何以基督的爱陪伴服事哀恸之人。3.【听见】文字事工持续关注最黑暗的角落，采访一个个出黑暗入光明的生命故事，如：失去独生子女的父母、吸毒过来人、寡妇孤儿，以及儿童爱滋关怀等。求神继续带领，使用【听见】，让更多人听到福音大能，见到生命真光！4.求主继续差派合适的全职工人到【听见】团队', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-10', createDate: '2017-03-10', endDate: '' }, { index: 89, publisher: '祖华', group: '教会', content: '1.为着J(教)H(会)的讲台信息代祷，求主赐下话语使用每次的讲台信息！2.为着JH的崇拜服侍（领诗 司琴）代祷，求主使得我们更加享受敬拜，使得JH崇拜也有进深和突破！为着小组崇拜来祷告，也需要更多学习和装备！接下来崇拜和司琴的学习求主带领！3.为着JH的探访事工代祷，求主兴起有探访负担的弟兄姐妹，完善探访事工，无论是疾病失亲还是慕道初信都有人参与关怀探访！有人专门去思考探访关怀事工！4.为着JH的后勤服侍代祷，求主兴起后勤方面有负担的同工能够带领弟兄姐妹确实能够管理好神的家！主日后是否预备饭食求主带领！5.为着JH弟兄姐妹的恩赐配搭 明确自己职责和托付代祷！6.为着JH的门训事工代祷，使得我们能够有合适的跟进支持体系！7.为着刚进入服侍的弟兄姐妹代祷，如何引导和训练陪伴帮助他们，求主赐下智慧及合适方式！', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-17', endDate: '' }, { index: 105, publisher: '祖华', group: '教会', content: '3月29日晚上七点半到九点半有小组崇拜的学习，求主带领各个小组辅导同工及有带领诗歌服侍的弟兄姐妹参与！也求主保守帮助青翠姐妹有智慧有能力的分享！', label: '事工', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-22', createDate: '2017-03-22', endDate: '2017-03-30' }, { index: 57, publisher: '金毅', group: '教会', content: '安溪有多个村、镇，教会荒凉，为中国农村教会复兴代祷。很多人在黑暗死荫幽谷中，求 神怜悯，求主复兴，愿神的福音广传', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-09', createDate: '2017-03-09', endDate: '' }, { index: 40, publisher: '陈杰', group: '信实', content: '陈杰:老家教会在我村里盖教堂的事情，之前土地所是说可以盖，但是上面的审核还没有批下来，现在邻近的居民说是会影响他们，想要上告拦阻~求主开路，除去一切的拦阻，能够依靠神在这件事情上得胜并且荣耀主的名。', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-01', createDate: '2017-03-01', endDate: '' }, { index: 21, publisher: '祖华', group: '教会', content: '东海越来越多老年人，基本都是外地过来的，甚是孤独，也是福音好土人群。求主兴起或差派工人向这些老人家传福音。', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 104, publisher: '祖华', group: '其它', content: '福州邹文星老师的父亲年后肺积水住院一个半月上周五回家观察治疗，医生不能确诊，现在积水基本没有了，找不出积水的原因，在家吃药治疗，昨天药物又过敏。请代祷', label: '疾病', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-22', createDate: '2017-03-22', endDate: '' }, { index: 79, publisher: '祖华', group: '但以理', content: '更新：春明弟兄：春明弟兄委托素芬姐妹去向他前妻郑月娥传福音，求主预备时间和机会！===>更新：为着春明弟兄信仰的清楚以及自己所经历的能够被主安慰代祷===>为着刚决志信主的春明弟兄代祷，情感经历使他常常陷入痛苦！求主救拔他！刚离异，自己承担照顾两个儿子的责任！求主供应他一切所需！', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-03-14', endDate: '' }, { index: 37, publisher: '齐雨佳', group: '毕业生', content: '更新：请大家为雨佳姐妹的姨奶奶的老伴宁启后的生命得救（还没传福音给他），身体治疗康复祷告：他心脏粥样硬化，已经住院一周多了，还没有出院，心脏可能要做支架。也求主预备当地教会的肢体去探访老人家，目前还没联系上芜湖当地的教会。===>更新：今天雨佳舅舅的儿子，雨佳表弟决志信主了。===>为雨佳亲人生命得救代祷：特别是奶奶和姨奶奶都七八十岁了，她们认识主，但还没有决志信主；爸爸去年已经决志了，求主带领他们去到教会，认识主，信靠主。', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-01', endDate: '' }, { index: 19, publisher: '董中民', group: '青草地', content: '更新：请为着辉彬祷告，坚固弟兄，带领他前面服侍的道路。继续将他的父母交托仰望，求主动工，求主开路！===>为辉彬和惠珍的婚姻祷告，为辉彬的父母得救祷告', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-20', createDate: '2017-02-11', endDate: '' }, { index: 107, publisher: '振营', group: '但以理', content: '更新：求主保守今天刚决志的周宝健弟兄和他的儿子周华文，求主坚固他们，护卫他们，让他们经历主的大能，也求主看顾周宝健弟兄的身体，亲自做医治的工作===>更新：感谢主，下午跟柯厝周宝健的儿子周华文传福音，带他作了决志。现在去他家除偶像，请代祷。===>请为师院后山柯厝村周宝键祷告，他得了癌症，在东海二院住院。他姐姐姐夫一家信主。我（振营）在他妻子去世前有带她决志祷告。今天下午去探访传了福音，劝他离弃偶像归向真神，接受耶稣救恩。他还未接受。他儿子（周华文，28岁，未婚）有虚心听福音。求主怜悯动工，拯救这一家。', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-23', endDate: '' }, { index: 11, publisher: '祖华', group: '但以理', content: '更新：为林田姐妹前面的道路代祷，求主亲自引导和预备！无论是回莆田还是留泉州，求主使其经历神的带领，为丽花能够完全信靠主，求主为丽花预备主内配偶===>青年团契弟兄姐妹婚姻也需要特别代祷 春节后 丽花 林田都压力大', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-02-08', endDate: '' }, { index: 53, publisher: '杜老师', group: '但以理', content: '更新：为振营兄妻子小梅姐祷告，为小梅姐的身体，为以琳的成长，能更加认识主===>为小梅祷告。她最近请了三天假调整精神和身体。求主让小梅操练身体、操练敬虔。多有密室亲近主的生活、多多户外锻炼，使身心灵刚强起来。', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-09', endDate: '' }, { index: 3, publisher: '董中民', group: '教会', content: '更新：现在已经有59个了===>更新：感谢主听我们的祷告，现在已经有56个人了，我们也继续凭着信心向主求，在赐给我们56个有负担代祷者，同事坚固现在已经有的代祷者，在祷告中经历上帝===>为我们教会建立长期的接力代祷事工祷告，我们正在建立代祷城墙事工(每天24小时，每次1小时，每周七天不间断的有人祷告)，如果你也有这样的异象可以在消息中心栏目中添加我微信回应，我们正在招募代祷者，目前我们已经有56个人报名，距离我们的最小目标56个人还差0个', label: '事工', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-02-08', endDate: '' }, { index: 34, publisher: '董中民', group: '麦田', content: '更新：现在有每天坚持读圣经，也没有那么着急了；晚箫姐妹：就是感觉很久没有读经没有祷告没有主日了，现在想开始重新回归基督徒的生活也不知道从哪里开始。而且最近因为找工作的事情内心很慌张，也不知道该怎么办', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-27', createDate: '2017-02-27', endDate: '' }, { index: 9, publisher: '杜老师', group: '但以理', content: '更新：有很大的进步。为杜老师的小孩一川代祷', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-09', createDate: '2017-02-08', endDate: '' }, { index: 49, publisher: '张鸿', group: '多加组', content: '更新:张鸿姐妹的爸爸背上的瘤因发炎破口，这几天流脓流血，但是瘤的主体部分没有随之排出。目前还在观察，如果还没进展，就得去医院处理了。张鸿爸爸因伤口疼痛，脾气也比较不好，心理承受的压力大，甚至一边吃药还一边喝酒。张鸿妈妈有鼓励和引导他自己要依靠神多祷告，但张鸿爸爸不是很相信神可以帮助他。求主怜悯，医治和帮助张鸿爸爸！===>请为张桂章（张鸿爸爸）能重生得救代祷。原本在晋江工作，今年打算回惠安工作，目前在家待业。求主为他预备一份合适的工作，释放他周日的脚步，使他可以参加主日敬拜能够真正认识神。也请为他的身体代祷，爱喝酒，背上的瘤也还在治疗中。谢谢！', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-08', createDate: '2017-03-08', endDate: '' }, { index: 82, publisher: '李月星', group: '但以理', content: '肯请弟兄姐妹为李月星的前夫代祷，我前夫陈新江7年不认父母，欠了200多万的债，依然在贷款，办信用卡吃喝玩乐，不懂得节制。求主饶恕他的罪，求主赶走恶者的欺骗和破坏，求主改变他的心意，让他悔改，让他跟父母和好。', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-15', createDate: '2017-03-15', endDate: '' }, { index: 69, publisher: '李月星', group: '但以理', content: '李月星姐妹的代祷事项：我工作的地方其他的伙伴都是信佛的，不让我读圣经，不让祷告，每天都在放佛歌，在这样环境下我内心很不平安，求主带领，求主保守', label: '福音', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 75, publisher: '徐丽婷', group: '橄榄树', content: '丽婷姐妹：为爸爸妈妈的身体代禱，为爸爸的脾气代禱；(爸爸妈妈哥哥还没信主，为他们得救代祷)。为爷爷奶奶的身体和属灵生命代禱，求主再次感动爷爷奶奶归向主', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 74, publisher: '徐丽婷', group: '橄榄树', content: '丽婷姐妹代祷事项：为哥哥的店代祷，求主赐给哥哥聪明智慧好好经营店，也为哥哥的感情代祷，求主赐给他一个善良贤惠的另一半，哥哥还没有信主', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 1, publisher: '亚丽', group: '青草地', content: '良善组映苇姐妹：由于母亲常年一个人居住，性格孤僻，不喜欢热闹，今年回去感觉情况不容乐观，有时会自言自语，举止怪异，出现短暂性的间接性的精神分裂，有时候会突然开心，有时候一动不动，初步判断是精神分裂，再加上常年饮酒，对身体还是有一定的刺激性的，现在的情况是我在外地上学，她一个人在家还要工作，很令人担心想要尽快找到解决的办法，患者性格固执是绝对不配合去医院面诊和治疗的，所以现在这件事让我不知道该怎么办了', label: '疾病', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-12', createDate: '2017-02-12', endDate: '' }, { index: 101, publisher: '恩泉', group: '活石', content: '林恩泉弟兄代祷事项：愿主为我的暑期社会实践开道路！愿那在我心里动了善工的来成全这工，求神特别的怜悯我的妈妈，同意我参与', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-03-21', endDate: '' }, { index: 24, publisher: '林梦雪', group: '麦田', content: '林梦雪代祷事项：林梦雪的爸爸林荣还没有接受主，并且爷爷奶奶很逼迫，不让他们有这个信仰，对她妈偏见很大她爸又比较听奶奶话，所以即使知道这信仰是好的也不跟他们一起，求主断开他身上一切锁链，并怜悯她的爷爷奶奶，带领她的家庭，使他们能够建立起家庭的祭坛；还有她的外公以及两个迷失的舅舅，外公曾经生了一场大病被神医治，可是后来因着这个信仰被街坊邻居笑话就远离神，甚至和世人一样参加一些迷信活动，两个舅舅又特别悖逆，求主使他们能够悔改', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 113, publisher: '董中民', group: '麦田', content: '林天生（师yuan初信弟兄）很软弱的我，恳请大家为我代祷。感觉有争战，今天辅导员把我叫过去叫我一定要去晚上一个奖项的评比活动，让我出席，和今晚学习有冲突了。 我不想说谎，坦言我晚上有学习（把握时间），可能有冲突。聊到是为了教会的活动。之后便开始了长达快50分钟的长谈。一直要我不能去教会。因为是不正规的。心里很难过。 说我身份特殊，（学院组织部部长以及党员）而且还带着几分威胁的口气。说我入会可能要撤掉我部长和党员的身份。到时候名字还会在参加非法宗教活动的名单上。 心里很难受。我的信仰在她面前被一点一点否定。我只能勉强先答应她之后会更多和我们学院的一个信教的老师联系，去正规的教堂。  晚上也必须得先去参加那个评比。希望早点结束，时间不要冲突。  拜托大家祷告，求主软化我辅导员的心，让她能够理解我。。父母亲还都没有信主，有一些反对。', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 87, publisher: '祖华', group: '教会', content: '林愉兄：请组织同工为周六小提班祷告。谢谢！', label: '事工', isUrgent: true, isLong: false, isEnd: true, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-17', endDate: '2017-03-19' }, { index: 68, publisher: '中民', group: '麦田', content: '麦田云萍姐妹：更新：论文已经通过===>希望论文答辩能够顺利通过 现在还没有很确定报考哪里  希望教招能考个理想的成绩', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 59, publisher: '波文', group: '闽科', content: '闽科荣飞弟兄的代祷事项：他今天去福州福师大了，准备参加明天上午的复试，因为他面对这样的事情，会让他很忧虑，使他的情绪偏向消极，也影响他的休息，所以他虽然决定留在闽科，但是他想面对这样的事情，尝试突破自己，所以他就去复试了，为他情绪平稳代祷，他的复试的情况代祷，也为他复试完后天下午会有个体检代祷，还有他决定复试回来，要去医院确诊下自己的情况，到底是什么原因，时间暂定周五(3.24)的早上===>更新：荣飞低沉的光景渐渐好转，目前考研的情况出来了，他过了福师大的分数线，所以现在他自己对留下来继续在我们学校做实验员还是去读研拿不定主意，他目前的决定是一边工作，一边准备考研的复试，但是对他复试过了，要不要去读研还是有点犹豫，求主帮助他能够做好决定代祷；===>荣飞弟兄的事代祷，他从初三开始有类似抑郁症的状态，到如今每逢秋冬都会落入这样消极、低落的状态，看待事情也都是偏向消极的，以至于做什么是都没有兴趣，他从去年的四月份信主，本以为不会再出现这种情况，但是当他过年回家，又出现了，本来今年他有望去福师大读研，但由于状态的影响，也不怎么想去，目前应聘了我们学校的实验员，在学校工作调整状态，关于他弃研留校的选择，目前家里人也有点失望，也求神能够医治他的这种光景，调整他父母对他的看法，他也有志服侍神，一直也是我（波文）陪伴他一起成长，也求神给我智慧，能够和他一起同奔天路。', label: '个人', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-03-09', endDate: '' }, { index: 61, publisher: '燕玲', group: '青草地', content: '请帮大四英语专业的姐妹下周六的专八考试代祷！求主赐下智慧，保守写题的每分每秒效率高一点！', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '', updateDate: '2017-03-10', createDate: '2017-03-10', endDate: '2017-03-19' }, { index: 102, publisher: '培灵', group: '但以理', content: '请大家为(海星小区旁)莲垵乡的蜜珠姐妹代祷。蜜珠忍受不了在夫家受苦待和压抑，前些天说想带孩子回惠安的娘家。今天发消息来说，丈夫提出离婚要赶她出家门，不让她带走小孩，蜜珠姐妹为此愁苦。蜜珠姐妹前两年信主，在恩福堂主日聚会，去年生产后因带小孩及家务缠身，经常不能去聚会，家里只有她一人信主，经常面对婆婆叫她敬奉偶像的无奈(未信主前迫于家庭和年龄压力而结婚)，求神在姐妹丈夫心里动工，也安慰姐妹，加添姐妹力量。', label: '家庭', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-03-21', endDate: '' }, { index: 27, publisher: '阳辉', group: '青草地', content: '请大家为刘俊辉（阳辉弟弟）祷告，使他可以信主。求主改变使他能够愿意交流，能够来聚会。保守我的父母，能够仰望交托主，他们很着急他的婚姻。', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-17', createDate: '2017-02-17', endDate: '' }, { index: 83, publisher: '朦朦', group: '多加组', content: '请大家为朦朦姐妹的暑期社会实践祷告，跟妈妈交流之后，妈妈表示尊重姐妹的决定，但是姐妹心里还是感觉不平安，求主指引姐妹前行的道路，除去姐妹心中的不安', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-16', createDate: '2017-03-16', endDate: '' }, { index: 91, publisher: '祖华', group: '但以理', content: '请大家为培灵姐妹的舅舅(王文凭)代祷，他家庭四散，出家多年，这一两年在南安康美一个寺院里服侍假神。姐妹和舅舅这些年极少联络，信主后常为他祷告。感谢主，因着他对自己姐姐(姐妹的母亲)的挂念，这一两年有多几次联系。求主耶稣的灵在他心中动工赐他信心，叫他有力量能调转脚步勇敢迈向基督，降伏于主，得真自由。', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 86, publisher: '祖华', group: '其它', content: '请大家为文焰祷告，今天去福清医院抽血，确认怀孕，甲亢复查结果很好，(tsh（甲亢指标）尚未完全恢复，但求主医治完全。)医生建议现在开始停药。接下来就是信心之旅，请大家帮忙祷告', label: '家庭', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-17', endDate: '' }, { index: 71, publisher: '振营', group: '但以理', content: '请弟兄姐妹为一个叫慎独的男生祷告，情绪一上来就会砸汽车玻璃、电脑手机屏幕。他父母因这个孩子来信主，但信心不足。求主驱逐搅扰的邪灵，并使他明白过来归到主名下。', label: '疾病', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 112, publisher: '祖华', group: '但以理', content: '请继续为奇鹏弟兄代祷，求主给他刚强壮胆的心能够勇敢跟其他亲人作见证，传福音，谢谢！', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 116, publisher: '刘媛', group: '教会', content: '请迫切为刘信姐妹前面的道路（带职or全职）祷告，四月初学习结束毕业。家人方面是希望她继续工作，姐妹也很迷茫，求神带领，让她听见从神而来的声音，愿神的旨意成就。也为姐妹的婚姻祷告，家人比较着急，求神预备。', label: '个人', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 72, publisher: '燕玲', group: '青草地', content: '请请为胡杨（燕玲姐妹朋友）祷告 她得了一种骨肉瘤的病，在北京治疗，这是一种癌症，很难医治，而这个病是和人的心情有关，如果她不那么抑郁得到医治的可能性大很多，可惜她前两年得了抑郁症，现在又得病，很怕她又心里很难过又得抑郁症，上次看她发坐轮椅的朋友圈问她她都不回我（她和燕玲弟弟是初中同学，他们之间有过节，不知道为何她恨我弟，所以也疏远燕玲了…）以上消息是从我们主日学老师那里得知的，她也是上帝的宝贝女儿，但是在生活中会比较自我，也不爱给人添麻烦，很怕她没有朋友陪伴，求主保守她医治她，派天使守护她，也求主让她在病痛中紧紧依靠主！求主开路让她愿意和我有更多的交流，让燕玲有机会去看望她！', label: '疾病', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 109, publisher: '林明', group: '多加组', content: '请为多加组文浩弟兄祷告，他的妈妈对于他的信仰并不是很支持，刚刚决志的他可能会受到家里很大的逼迫，求主保守他的心，不因着逼迫而跌倒，学习在各样的光景中依靠神，也求主在他的家人中做奇妙的工作，接受他将福音带回到家中', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-23', createDate: '2017-03-23', endDate: '' }, { index: 106, publisher: '恩泉', group: '活石', content: '请为恩泉弟兄的姐姐婚姻代祷，求主预备合适的配偶，更愿主把寻找弟兄这样的负担放在她心中。', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-23', createDate: '2017-03-23', endDate: '' }, { index: 103, publisher: '凯丽', group: '闽科', content: '请为凯丽姐妹的d(短)x(宣)代祷，还没考虑清楚要不要去，求主带领前行的方向，也为凯丽的妈妈祷告，求主柔软她的心，支持凯丽的dx', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-22', createDate: '2017-03-22', endDate: '' }, { index: 39, publisher: '林恩泉', group: '教会', content: '请为我们教会所有的应届毕业生代祷！大家都在实习或正在找工作，还有准备考编的。我们多数处在迷茫和社会的各种压力中，信仰常受到冲击。请代祷！还有一些弟兄姐妹有异象留下来服侍的，求神开道路并预备。', label: '个人', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-01', createDate: '2017-03-01', endDate: '' }, { index: 48, publisher: '定露', group: '信实', content: '请为信实组刘伟祷告！他曾经女朋友带他信主，大一下到小组当中，也相对稳定，还没有受洗。这学期想和他一起学习受洗课程。这几天他父亲过世，他回去了，几次都是草草聊了两句，我（定露）很担心他在家里遭到逼迫，而产生对神的动摇。求神坚固他对神的认识，不至于坠落在耶稣基督的恩典当中。', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-07', createDate: '2017-03-07', endDate: '' }, { index: 78, publisher: '祖华', group: '但以理', content: '请为秀梅姐妹的身体代祷，刚怀孕反应比较强烈，胃痛，而且要照顾天天，祖华弟兄比较忙碌！求主使她有胃口吃饭！求主刚强扶持！', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-14', createDate: '2017-03-14', endDate: '' }, { index: 98, publisher: '祖华', group: '但以理', content: '请为雅玲姐妹的爷爷奶奶及爸爸的信主代祷', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 33, publisher: '祖华', group: '教会', content: '请为主日讲台安排及主日敬拜安排代祷！目前讲台和主日带敬拜都需要兴起工人！如果你在祷告中对这项服侍有感动，可以联系我', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-01', createDate: '2017-02-27', endDate: '' }, { index: 56, publisher: '杜老师', group: '教会', content: '求神复兴教会的祷告，让我们在祷告中看见弟兄姐妹的困难，激励我们探访的心，交通的心，安慰的心，怜悯的心，祈求的心。求神赐下异象，并让我们都能清晰地看到，经历到。求神亲自带领一切', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-09', createDate: '2017-03-09', endDate: '' }, { index: 77, publisher: '吴林明', group: '教会', content: '求主保守每一位参与祷告的弟兄姐妹，加添我们力量，在长时间的祷告中，有从神来的力量去完成，也求主保守我们的祷告不要沦落为形式，有爱人的心在这当中', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 16, publisher: '振营', group: '但以理', content: '求主继续在振营兄父亲心中工作。使主让他能够早日接受福音', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 76, publisher: '徐丽婷', group: '橄榄树', content: '求主指引丽婷姐妹前行的路，为她的暑期实践代禱', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 110, publisher: '向锋', group: '其它', content: '苏州教会的：成进泽、夜里一点多钟脑内出血送进重症监护室抢救，现在很危险，求主施恩怜悯，彰显医治的大能，使孩子转危为安，使灵魂体都能得救，，医生刚找家属谈话，瞳孔已放大，身体也没体温了，建议把孩子拉回去，但父母很伤心，妈妈无法承受这个事实，求主安慰父母的心', label: '疾病', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 62, publisher: '爱珍', group: '信实', content: '为爱珍奶奶的身体健康和生命代祷，奶奶一直不断住院身体患有冠心病、脑出血和糖尿病，不知道她的身体还能撑多久。之前因为摔了，所以没办法自理，叔叔家条件不是特别好，没有办法请保姆照顾，我爸爸又是常年在外没有办法照顾，所以不知道奶奶以后谁来照顾。奶奶有说信主，但具体生命如何我不知道。求主托住她的身体，以及在安排人照顾奶奶的事上，求主做带领的工作', label: '疾病', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-10', createDate: '2017-03-10', endDate: '' }, { index: 92, publisher: '祖华', group: '但以理', content: '为聪晓的爸爸在江苏工作的平安及能够信主代祷，为聪晓的姐姐、哥哥及他自己的寻找主内对象代祷', label: '家庭', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 67, publisher: '守玉', group: '多加组', content: '为多加组秀丽姐妹代祷，她家里人让她这学期每两三周要去厦门一次，跟她表姐学淘宝网店管理之类的，而且是周末去。她很不情愿，但是她父母一直强烈要求，面对这样的情况她也不知道怎么办！为她的生命代祷，也求主赐给她智慧可以说服父母，可以有稳定的聚会时间。', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 99, publisher: '祖华', group: '教会', content: '为各团契每周的查经祷告会以及主日敬拜代祷', label: '事工', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 22, publisher: '杰军', group: '活石', content: '为杰军的姐姐杰梅代祷，求主预备合适配偶', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 108, publisher: '振营', group: '但以理', content: '为柯厝林秀姐妹的丈夫周维文、儿子周加鑫、女儿周玉萍祷告，他们有去过教会，因着压力不再聚会。求主在柯厝兴起祂奇妙的工作', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-23', createDate: '2017-03-23', endDate: '' }, { index: 25, publisher: '锦辉', group: '活石', content: '为黎大锦辉弟兄代祷,身体胃病影响很大,第一代基督徒,家庭争战很大!', label: '疾病', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 95, publisher: '祖华', group: '但以理', content: '为马星弟兄的信仰及情感婚姻代祷', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 115, publisher: '董中民', group: '麦田', content: '为麦田团契参与以勒组的弟兄姐妹祷告：艺璇（姐妹）、美杰（姐妹）；天生（弟兄），求主帮助保守他们的信心和家人，天生家人没有信主', label: '事工', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 93, publisher: '祖华', group: '但以理', content: '为培灵姐妹找工作及婚姻的预备代祷', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 23, publisher: '祖华', group: '但以理', content: '为邱玉梅及妹夫郑毅洲代祷，结婚四年还没有怀孕，求主赐下敬虔后裔！目前准备检查身体，调理身体，是否进行试管婴儿！ ', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-04', createDate: '2017-02-11', endDate: '' }, { index: 29, publisher: '守玉', group: '多加组', content: '为守玉的姐姐的婚姻祷告，姐姐89年的，一直没有对象，为这件姐姐生命代祷，求神怜悯姐姐，使姐姐愿意交托信任神。守玉爸爸虽然没有信主，但却愿意为这女儿的婚姻祷告，求神怜悯，开路。让守玉爸爸在姐姐婚姻上经历神，能够信主。', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-20', createDate: '2017-02-20', endDate: '' }, { index: 97, publisher: '祖华', group: '但以理', content: '为舒寒姐妹的信仰生活（读经、祷告、灵修）以及情感问题建立代祷', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 54, publisher: '杜老师', group: '但以理', content: '为文生夫妻，金毅夫妻代祷，祈求神赐给他们后裔，顺利怀孕。', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-09', createDate: '2017-03-09', endDate: '' }, { index: 70, publisher: '中民', group: '教会', content: '为我们的168守望城墙能在教会中有一个祷告教导的聚会祷告，预备每个代祷者的心，学习成为一个合神心意的守望者，为这样的聚会的时间和参加人员还有带领着祷告，求神为我们预备', label: '事工', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 18, publisher: '董中民', group: '青草地', content: '为小宇和亚丽的婚姻祷告，求主带领他们的结婚时间，求主让亚丽的父母能够早日听见福音悔改得救，他们打算在6月左右结婚，但是现在时间还不能定下来，已经订婚', label: '婚姻', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 96, publisher: '祖华', group: '但以理', content: '为新来的[宏图、欢欢、晓伦、秋阳]这些姐妹能够稳定团契生活代祷', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 85, publisher: '祖华', group: '但以理', content: '为秀梅的同学段一方姐妹代祷，因着情感的问题已经信主，在杭州稳定聚会，求主加添她力量和信心！现独自带着孩子，希望柔软她丈夫的心，让她丈夫能够悔改信主！', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-17', endDate: '' }, { index: 115, publisher: '董中民', group: '麦田', content: '为玉兰姐妹代祷：五月份生产，求主保守胎儿健康，并且从小认识敬畏主，保守生产的过程，减轻疼痛，玉兰最近牙齿有些坏了，孕期不能去看牙医，希望在孕期不要出现疼痛。', label: '个人', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '' }, { index: 66, publisher: '金毅', group: '但以理', content: '为远娣姐妹家人得救祷告，目前最小的妹妹已经信主，爷爷奶奶、爸爸妈妈、大弟小弟大妹听过福音但是还没信，求神带领！愿父神旨意成就！', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 65, publisher: '金毅', group: '但以理', content: '为远娣姐妹可以找到合适的教会，新工作有爱心智慧，可以很好适应国内环境和人际关系代祷，之前几年在国外侍奉。', label: '个人', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-13', createDate: '2017-03-13', endDate: '' }, { index: 114, publisher: '董中民', group: '麦田', content: '为这周天（3.26）天气祷告，麦田团契组织去海丝公园游玩团建，天气预报下雨，求主预备天气和参加的弟兄姐妹的，让大家更愿意参加小组', label: '事工', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-24', createDate: '2017-03-24', endDate: '2017-03-27' }, { index: 90, publisher: '祖华', group: '但以理', content: '为着建业能够清楚信仰，稳定聚会代祷！很久没来，现在有过来！需要求主预备合适配偶！', label: '福音', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-18', createDate: '2017-03-18', endDate: '' }, { index: 17, publisher: '振营', group: '但以理', content: '为振营兄楼下蔡老师一家祷告，是慕道友', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 20, publisher: '祖华', group: '但以理', content: '为祖华的爸爸 杰军的爸爸 道辉秀梅的爸爸信仰能够清楚，能够确实认识神代祷！', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-02-11', createDate: '2017-02-11', endDate: '' }, { index: 81, publisher: '中民', group: '教会', content: '希望我们能为着初中高中生团契的建立祷告，学生的福音工作需要从初中开始，特别是已经信主的家庭小孩，求主带领，不要让这个世代的价值把属于主的小孩带走，在祷告中求主兴起有异象的工人', label: '事工', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-14', createDate: '2017-03-14', endDate: '' }, { index: 100, publisher: '晓荣', group: '橄榄树', content: '晓荣姐妹的外甥恩泽的眼睛弱视加斜视蛮严重的，医生说可能再严重点就会看不见。望代祷，希望主医治。', label: '疾病', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-20', createDate: '2017-03-20', endDate: '' }, { index: 44, publisher: '谢林莉', group: '但以理', content: '谢姐妹代祷事项：感谢听祷告的天父！谢林莉姐妹弟媳的父亲已平安出院。求主继续动工让姐妹的哥哥（谢林茂）弟弟（谢林峰）接受福音。最近报名参加"丧亲家属支持小组"，先生过世后一直带着14岁的侄女李嘉欣（丈夫弟弟的女儿，四岁就带在身边），失去丈夫快四年，一直在神的恩典中坚持工作和养育孩子，丈夫过世以后受到诸多不公平待遇，嘉欣现在又出现早恋的倾向，且事事作对不听话，最近一年来觉得有些坚持不下去了（想把嘉欣归还她在湖南农村的父母抚养）。求主帮助姐妹能坚持、坚强、有智慧带好嘉欣，让姐妹参加这次小组后生命能够转变，求神改变嘉欣能真正的信主。', label: '家庭', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-03', endDate: '' }, { index: 2, publisher: '谢林莉', group: '但以理', content: '谢林莉姐妹代祷事项：求主帮助谢林莉姐妹侄子谢全鑫（15岁初中二年级）戒除网瘾，全鑫之前有听过福音，求主动工在他身上，让全鑫能跟着奶奶（我妈妈）去到教会真正接受福音。带领保守耘锋、雨诺在深圳创业顺利，成为家人的祝福。帮助大三的渝欣回到教会。', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-03', createDate: '2017-02-10', endDate: '' }, { index: 46, publisher: '祖华', group: '但以理', content: '秀梅这周周一到周四去漳州学习，为她！也为我（祖华）带孩子及服侍代祷', label: '', isUrgent: true, isLong: false, isEnd: true, archiveContent: '', updateDate: '2017-03-07', createDate: '2017-03-07', endDate: '2017-03-17' }, { index: 55, publisher: '杜老师', group: '但以理', content: '徐燕姐妹一家好久没来教会，求主保守…', label: '福音', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-09', createDate: '2017-03-09', endDate: '' }, { index: 63, publisher: '亚丽', group: '青草地', content: '亚丽姐妹代祷事项:请弟兄姐妹为我爸爸（养父）祷告，他在惠州打工有10几年了，在这10年间妈妈（养母）一个人在家带两个弟弟，非常不容易，十年也发生了很多事情，慢慢地她整个人都变了，变得非常多疑，会讲一些让人难以相信的事情（比如她会说政府有给她一辆小轿车，结果被奶奶和姑姑背着她领走了，我知道这个是根本不可能的，诸如此类……）。她极度的缺乏安全感，感觉整个世界的人都要害她，以前她鼻子上长了疮，她说是爸爸故意害她的。其实爸爸是很老实的人，挣得钱都打给家里，但妈妈还是仍然会说他把钱都私自藏起来，甚至会说他把钱给了别的女人，所以她会一直嫌弃，怀疑，辱骂，怨恨我爸爸，总是讲一些恶毒甚至是咒诅的话，没有丝毫尊重。爸爸今年好不容易回家过年，但是春节期间在家在我妈面前却是一句话都不敢讲，因为生怕说错话，看到他那样我非常心疼爸爸，但也无法帮到他（因为我10岁以后回到亲生父母身边，跟他们相处的时间也很少，只能偶尔回去看他们一下）。我在大学信主后，借着一位姐妹帮爸爸联系到深圳沈弟兄那边的教会，很感恩，不久后爸爸就信主还受洗了。但是由于惠州那个地方没有聚会地点，每次爸爸聚会都要坐车去深圳，爸爸会心疼来回的路费，也会觉得自己家的事情很丢人，所以不爱开口跟人分享和交流，所以，他也很久没有去聚会了。但是感恩的是沈弟兄也一直在关怀着我爸，每天发经文、文章和诗歌给他。妈妈的强势和暴脾气，爸爸的无奈和老实，最痛苦和受伤害的还是我那两个弟弟，妈妈非常爱两个弟弟，但是她多疑和强势的性格会包办一切，不让弟弟独自去做一些事情，导致弟弟性格很内向，不爱与人说话，求主医治破碎的家庭，释放妈妈，刚强爸爸，败坏那恶者在妈妈身上的攻击（她有信主，但是信仰极端，认为凡是聚会时会拍手跳舞的教会都是异端……只有她聚会的那个教会可信，我们两个谈论信仰就会吵起来，根本没办法和她讨论，她认为她说的都是对的，我不能说一个不字）。两个弟弟都愿意信主，但在妈妈的身边永远无法真正认识主，求主在我的家里掌权，让爸爸妈妈弟弟都能真正认识主，有盼望，有真正的平安和喜乐。求主使用我，帮助家人认识主。', label: '家庭', isUrgent: false, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-10', createDate: '2017-03-10', endDate: '' }, { index: 47, publisher: '张鸿', group: '教会', content: '以勒小组代祷更新：现第一阶段招募已经完成，特别为团队中弟兄的兴起代祷！闽科团队需要特别的代祷，现在无人参与！以勒组需要后勤接待同工，求主预备！栽培架构基本思路清晰，但求主预备师资及同工积极参与！本周六（25日）及清明三天的把握时机课程请切切代祷！===>1.请为以勒组的招募守望，愿神兴起有心志的工人2.为接下来几个月会陆续进行的培训和团建（内容、时间、老师、方式）来守望3.为以勒同工祷告，我们是团队服侍，求主兴起工人，也请为团队合一代祷4.今年的服侍地点会有调整，求主预备', label: '事工', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-21', createDate: '2017-03-07', endDate: '' }, { index: 64, publisher: '桂云', group: '主日学', content: '主日学配搭老师很多，但是不固定，为招募新主日学老师代祷，将愿意的心放在新主日学老师心中。', label: '事工', isUrgent: true, isLong: true, isEnd: false, archiveContent: '', updateDate: '2017-03-10', createDate: '2017-03-10', endDate: '' }, { index: 88, publisher: '振营', group: '其它', content: '邹穂老师呼吁为芳琪姐妹祷告，她正经历邪灵攻击。另她看荣格《红书》见过幻象，內容几乎全是通灵和招魂术炼金术，很可怕', label: '疾病', isUrgent: true, isLong: false, isEnd: false, archiveContent: '', updateDate: '2017-03-17', createDate: '2017-03-17', endDate: '' }];

