
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import Icon from 'vue-awesome/components/Icon.vue'
import {code} from './db/info.js';

export default {
  name: 'app',
  components: {
    Tabbar,
    TabbarItem,
    ViewBox,
    XHeader,
    Icon
  },
  data() {
    return {
      code: code
    }
  },
  methods: {
    scrollTop() {
      this.$refs.viewBox.scrollTop(0)
    }
  },
  computed: {
    leftOptions() {
      let path = this.$route.path
      let isShow = ['/Prayer/urgent', '/Prayer/today', '/List', '/Info'].find(function(value, index, arr) {
        return path === value
      })
      return {
        showBack: !isShow
      }
    },
    title() {
      if (this.$route.meta.title) {
        return this.$route.meta.title;
      } else {
        let title = this.$route.params.type;
        if (title === 'long') {
          title = '长期事项';
        } else if (title === 'all') {
          title = '全部事项';
        } else if (title === 'end') {
          title = '结束事项';
        } else if (title === 'today') {
          title = '今日Prayer';
        } else if (title === 'urgent') {
          title = '紧急Prayer';
        }
        return title;
      }
    }
  },
  created: function() {
    let _code = this.$utils.storage.getCode();
    if (_code !== this.code) {
      this.$utils.storage.savePrayInfoIsRead('{}');
      this.$utils.storage.saveCode(this.code);
    }
  }
}



