{index:63,publisher:'亚丽',group:'青草地',content:'亚丽姐妹代祷事项:请弟兄姐妹为我爸爸（养父）祷告，他在惠州打工有10几年了，在这10年间妈妈（养母）一个人在家带两个弟弟，非常不容易，十年也发生了很多事情，慢慢地她整个人都变了，变得非常多疑，会讲一些让人难以相信的事情（比如她会说政府有给她一辆小轿车，结果被奶奶和姑姑背着她领走了，我知道这个是根本不可能的，诸如此类……）。她极度的缺乏安全感，感觉整个世界的人都要害她，以前她鼻子上长了疮，她说是爸爸故意害她的。其实爸爸是很老实的人，挣得钱都打给家里，但妈妈还是仍然会说他把钱都私自藏起来，甚至会说他把钱给了别的女人，所以她会一直嫌弃，怀疑，辱骂，怨恨我爸爸，总是讲一些恶毒甚至是咒诅的话，没有丝毫尊重。爸爸今年好不容易回家过年，但是春节期间在家在我妈面前却是一句话都不敢讲，因为生怕说错话，看到他那样我非常心疼爸爸，但也无法帮到他（因为我10岁以后回到亲生父母身边，跟他们相处的时间也很少，只能偶尔回去看他们一下）。我在大学信主后，借着一位姐妹帮爸爸联系到深圳沈弟兄那边的教会，很感恩，不久后爸爸就信主还受洗了。但是由于惠州那个地方没有聚会地点，每次爸爸聚会都要坐车去深圳，爸爸会心疼来回的路费，也会觉得自己家的事情很丢人，所以不爱开口跟人分享和交流，所以，他也很久没有去聚会了。但是感恩的是沈弟兄也一直在关怀着我爸，每天发经文、文章和诗歌给他。妈妈的强势和暴脾气，爸爸的无奈和老实，最痛苦和受伤害的还是我那两个弟弟，妈妈非常爱两个弟弟，但是她多疑和强势的性格会包办一切，不让弟弟独自去做一些事情，导致弟弟性格很内向，不爱与人说话，求主医治破碎的家庭，释放妈妈，刚强爸爸，败坏那恶者在妈妈身上的攻击（她有信主，但是信仰极端，认为凡是聚会时会拍手跳舞的教会都是异端……只有她聚会的那个教会可信，我们两个谈论信仰就会吵起来，根本没办法和她讨论，她认为她说的都是对的，我不能说一个不字）。两个弟弟都愿意信主，但在妈妈的身边永远无法真正认识主，求主在我的家里掌权，让爸爸妈妈弟弟都能真正认识主，有盼望，有真正的平安和喜乐。求主使用我，帮助家人认识主。',label:'家庭',isUrgent:false,isLong:true,isEnd:false,archiveContent:'',updateDate:'2017-03-10',createDate:'2017-03-10',endDate:''},
{index:47,publisher:'张鸿',group:'教会',content:'以勒小组代祷更新：现第一阶段招募已经完成，特别为团队中弟兄的兴起代祷！闽科团队需要特别的代祷，现在无人参与！以勒组需要后勤接待同工，求主预备！栽培架构基本思路清晰，但求主预备师资及同工积极参与！本周六（25日）及清明三天的把握时机课程请切切代祷！===>1.请为以勒组的招募守望，愿神兴起有心志的工人2.为接下来几个月会陆续进行的培训和团建（内容、时间、老师、方式）来守望3.为以勒同工祷告，我们是团队服侍，求主兴起工人，也请为团队合一代祷4.今年的服侍地点会有调整，求主预备',label:'事工',isUrgent:true,isLong:true,isEnd:false,archiveContent:'',updateDate:'2017-03-21',createDate:'2017-03-07',endDate:''},
{index:64,publisher:'桂云',group:'主日学',content:'主日学配搭老师很多，但是不固定，为招募新主日学老师代祷，将愿意的心放在新主日学老师心中。',label:'事工',isUrgent:true,isLong:true,isEnd:false,archiveContent:'',updateDate:'2017-03-10',createDate:'2017-03-10',endDate:''},
{index:88,publisher:'振营',group:'其它',content:'邹穂老师呼吁为芳琪姐妹祷告，她正经历邪灵攻击。另她看荣格《红书》见过幻象，內容几乎全是通灵和招魂术炼金术，很可怕',label:'疾病',isUrgent:true,isLong:false,isEnd:false,archiveContent:'',updateDate:'2017-03-17',createDate:'2017-03-17',endDate:''}
